<template>
	<layout>
		<div class="d-flex">
			<h1 class="text-h4">العمليات</h1>
			<v-spacer></v-spacer>
		</div>
		<v-divider class="my-4"></v-divider>
		<v-card>
			<v-toolbar flat color="white">
				<v-text-field v-model="search" label="بحث" dense outlined single-line hide-details append-icon="mdi-magnify"></v-text-field>
				<v-spacer></v-spacer>
				<v-dialog v-model="invoiceDialog" max-width="700px" @click:outside="closeForm">
					<edit-invoice-form
						:key="invoiceKey"
						ref="invoiceForm"
						:invoice="invoice"
						@canceled="closeForm"
						@submited="saveResult"
					></edit-invoice-form>
				</v-dialog>
			</v-toolbar>
		</v-card>
		<v-data-table :headers="headers" :loading="isLoading" :items="invoices" sort-by="id" sort-desc class="mt-8 elevation-16" :search="search">
			<template v-slot:[`item.actions`]="{ item }">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-btn dark small min-width="40" color="success" @click="editInvoice(item)" v-on="on">
							<v-icon size="18">mdi-pencil</v-icon>
						</v-btn>
					</template>
					<span> النتيجة </span>
				</v-tooltip>
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-btn dark small min-width="40" class="mx-3" color="primary" @click="printResult(item)" v-on="on">
							<v-icon size="18">mdi-printer</v-icon>
						</v-btn>
					</template>
					<span> طباعة النتيجة </span>
				</v-tooltip>
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-btn dark small min-width="40" color="info" @click="generateCode(item)" v-on="on">
							<v-icon size="18">mdi-qrcode-plus</v-icon>
						</v-btn>
					</template>
					<span> QR Code </span>
				</v-tooltip>
			</template>
			<template v-slot:[`item.delivery_at`]="{ value }">
				{{ new Date(value).toLocaleDateString() }}
			</template>
		</v-data-table>

		<v-dialog v-model="alertDialog" persistent max-width="350">
			<v-card>
				<v-card-title>مبلغ متبقي</v-card-title>
				<v-divider></v-divider>
				<div class="pa-5">
					يرجى دفع باقي المبلغ قبل التمكن من طباعة النتيجة
				</div>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="primary" text @click="alertDialog = false">
						OK
					</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</layout>
</template>

<script>
import Layout from './layout/Layout.vue';
import { invoicesComputed, invoicesActions, resultsActions } from '../state/mapper';
import EditInvoiceForm from '../components/invoices/edit-invoice-form.vue';
import printJS from 'print-js';

export default {
	name: 'Invoices',
	components: { Layout, EditInvoiceForm },
	data: () => ({
		search: '',
		isLoading: false,
		isEditMode: false,
		invoiceDialog: false,
		alertDialog: false,
		invoiceKey: 0,
		invoice: {
			amount: '',
			total_amount: '',
			remain: '',
		},
		items: [],
		total: 0,
		options: {},
	}),

	computed: {
		...invoicesComputed,
		headers() {
			return [
				{ text: 'رقم العملية', value: 'id' },
				{ text: 'اسم العميل', value: 'name' },
				{ text: 'اسم الفحص', value: 'test' },
				{ text: 'المبلغ المتبقي', value: 'remain' },
				{ text: 'تاريخ التسليم', value: 'delivery_at' },
				{ text: 'إدارة', value: 'actions', sortable: false },
			];
		},
	},

	created() {
		this.loadData();
	},

	methods: {
		...invoicesActions,
		...resultsActions,
		async loadData() {
			try {
				this.isLoading = true;
				await this.getInvoicesAction();
				this.isLoading = false;
			} catch (error) {
				this.$VAlert.error('عذرا حدث خطأ!');
				console.log(error);
				this.isLoading = false;
			}
		},
		editInvoice(invoice) {
			this.invoiceKey++;
			Object.assign(this.invoice, invoice);
			this.invoiceDialog = true;
		},
		async saveResult({ data, isEdit, id }) {
			try {
				this.isLoading = true;
				isEdit ? await this.editResultAction({ resultData: data, resultId: id }) : await this.saveResultAction(data);
				this.$VAlert.success('تم حفظ النتيجة');
				this.isLoading = false;
			} catch (error) {
				this.$VAlert.error('عذرا حدث خطأ!');
				this.isLoading = false;
			}
		},

		isDelivered(item) {
			return item.delivered ? 'green lighten-4' : '';
		},

		async generateCode(item) {
			try {
				await this.getResultAction(item.id);
				printJS({
					printable: `/print/qrcode/${item.id}`,
					type: 'html',
					showModal: true,
				});
			} catch (error) {
				this.$VAlert.info('لم يتم ادخال النتيجة بعد!');
			}
		},

		async printResult(item) {
			if (item.remain !== 0) {
				this.alertDialog = true;
				return;
			}

			try {
				await this.getResultAction(item.id);
				printJS({
					printable: `/print/result/${item.id}`,
					type: 'pdf',
				});
			} catch (error) {
				this.$VAlert.info('لم يتم ادخال النتيجة بعد!');
			}
		},

		closeForm() {
			this.$refs.invoiceForm.reset();
			this.isEditMode = false;
			this.invoiceDialog = false;
		},
	},
};
</script>

<style lang="scss" scoped></style>
